  --dark-brown: #a64a23;
  --middle-brown: #f2cf9d;
  --light-brown: #94714f;
  --dark-gray: #362d27;
  --middle-gray: #cfc7ba;
  --light-gray: #f7f4f2;
}
.text-gray {
  color: darkslategray;
}
.bg-middle-gray {
background-color: #cfc7ba !important;
background-color: var(--middle-gray) !important;
}
.bg-light-gray {
background-color: #f7f4f2 !important;
background-color: var(--light-gray) !important;
}
.bg-dark-brown {
background-color: #a64a23 !important;
background-color: var(--dark-brown) !important;
}
.bg-middle-brown {
background-color: #f2cf9d !important;
background-color: var(--middle-brown) !important;
}
.bg-light-brown {
background-color: #94714f !important;
background-color: var(--light-brown) !important;
}
.bg-transparent {
background-color: transparent !important;
}
.text-dark-gray {
  color: #362d27 !important;
