h1,
h2,
h3,
h4,
h5,
h6 {
font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !important;
}

body {
font-family: Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !important;
}
button, a, input[type="submit"] {
  font-family: 'Roboto Condensed' !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.bg-header{
  background-color: #343a40 !important;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.corpo {
  position: relative;
}
.corpo::before{
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  content: "";
  width: 100%;
  height: 100%;
  background-color: #e9ebed;
}
@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#header-top {
    display: none;
}
#header-top .nav-link {
    color: darkgray;
}
#header-top .nav-link:hover {
    color: var(--middle-brown);
}

#header-top .social-icons .nav-link {
    color: var(--middle-brown);
    transition: all 0.3s ease-out;
}
#header-top .social-icons .nav-link:hover {
    background-color: var(--dark-brown);
    color:white;
}
#categorias .nav-link, #categorias button {
    color: var(--middle-gray);
}
#categorias button {
    box-shadow: none !important;
}
.nav-item.active a {
    color:white !important;
}
#categorias .nav-link:hover, #categorias button:hover {
    color: var(--light-gray) !important;
}
.btn-reset {
    border:none !important;
    background: transparent !important;
    outline:none !important;
    padding: 0px;
    margin: 0px;
}
.dropdown-menu {
    padding: 0px !important;
    overflow: hidden;
}
.dropdown-menu .dropdown-item:hover {
    background-color: var(--middle-brown) !important;
    color:var(--dark-brown) !important;
}

.input-quantity {
    width: 60px;
    padding: 0px !important;
    text-align: center;
    background: transparent;
    border: none !important;
    outline: none !important;
}
.btn-reset {
    outline: none !important;
    border: none !important;
    box-shadow: none !important;
}
#quantity {
    border-right: 2px solid var(--middle-brown);
}
#product-image {
    display:block;
    margin: 0 auto;
    max-height: 550px;
}
.product-esp-info {
    display:flex;
    margin-bottom: 1rem;
    background-color: var(--light);
    border-radius: 10px;
    overflow: hidden;
}
.product-info-icon {
    display:flex;
    justify-content: center;
    align-items: start;
    background-color: var(--middle-brown);
    min-width: 65px;    
}
.product-info-icon i {
    font-size: 1.5rem;
