}
.product-info-text {
    padding: 0.5rem 1rem;
}
.product-info-text h2{
    color: var(--dark-brown);
    margin: 0;
}
.product-info-text p {
    margin: 0;
}
.react-slideshow-container:hover .nav {
  opacity: 1;
}
.indicators {
  margin-top: 0px !important;
  transform: translateY(-100%);
}
.indicators .active {
  background-color: var(--dark-brown) !important;
}
.react-slideshow-container .nav {
    height: 50px !important;
    opacity: 0;
    transition:opacity 0.3s ease-out;
}
.react-slideshow-container .nav:first-child {
  border-radius:0px 5px 5px  0px !important;
}
.react-slideshow-container .nav:last-child {
  border-radius:5px 0px 0px  5px !important;

}
.section {
    margin-bottom: 3rem;
    border-radius: 0.25rem;
}
.section:last-child {
    margin-bottom: 0px;
}
.section .carousel-control-prev, .section .carousel-control-next {
    display: none;
    transition: opacity 0.5s ease;
}
.carousel:hover .carousel-control-prev, .carousel:hover .carousel-control-next {
    display:flex;
}
.carousel-control-prev span, .carousel-control-next span{
    background-color: var(--middle-brown);
    width: 50px;
    height: 50px;
    border-radius:50%;
    display: block;
    background-size: 50% !important;
}
.carousel-control-next span{
    background-position-x: 60% !important;
}
.carousel-control-prev span{
    background-position-x: 40% !important;
}
.section .carousel-fade .carousel-item {
    transition: opacity 0.5s ease !important;
}
.section .carousel-indicators {
    margin-bottom: 0;
    transform: translateY(150%);
}
.section .carousel-indicators li {
    border-radius: 50%;
    width: 10px;
    height: 10px;
}
@media only screen and (max-width: 576px) {
    .carousel-control-prev span, .carousel-control-next span{
        display: none !important;
    }
}
.product-wrapper {
    position: relative;
    padding: 0.5rem;
    border-radius: 0.25rem;
}
@media screen and (max-width: 1224px) {
    .product-wrapper {
        margin-bottom: 0.5rem;
    }
}
h3 {
    font-style: unset !important;
}
.old-price {
    text-decoration: line-through;
}
.product-img-wrapper img {
    height: 240px;
    max-width: 80px;
}



.categorias a {
    color: var(--middle-gray);
    text-decoration: none !important;
    outline: none !important;
    transition: color 0.3s ease-out;
}
.categorias a:hover {
    color: var(--dark-brown);
}
.categoria, .show-more {
    border-radius: .25rem !important;
    overflow: hidden;
}
.categoria {
    background-color:white;
    transition: box-shadow 0.3s ease-out;
    margin-bottom: 1rem;
}
.categoria:hover {
    box-shadow:  0 1rem 3rem rgba(0,0,0,.175) !important; 
}
.categoria h3 {
    text-transform: capitalize;
    margin: 0px;
    background-color:var(--dark-brown);
    padding: 1rem;
    color:white;
    text-align: center;
}
.img-categoria {
    height: auto;
    width: 100%;
}
.show-more {
    height: calc(100% - 1rem);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: var(--light-gray);
    padding:0.5rem 1rem;
}
@media (min-width: 576px) {
    .img-categoria {
        width: 225px;
        height: 150px;
    }
}

.show-more i {
    font-size: 40pt;
    order: 1;
}
.show-more h3 {
    margin-bottom: 0px;
}
