    }
}
@media screen and (min-width: 992px) {
    .cart {
        overflow: visible;
    }
    .cart-product-delete {
        width: unset;
    }
    .bg-cart-details, .cart-details::after, .cart-details::before{
        display:block;
    }
    .cart-details {
        background-color: unset !important;
        order: 0;
    }
}
.img-tamanho{
